.main-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 40px 50px 0;
	width: 100%;
	position: fixed;
	top: 0;
	left: 50%;
	transform: translateX(-50%);
	z-index: 10;
	@include lg {
		padding: 15px 15px 0;
		position: absolute; }

	&__hamburger {
		margin-left: auto;
		display: flex;
		@include min_lg {
			opacity: 0; } }

	&__logo {
		@include min_lg {
			transition-delay: 0.8s; } } }
