.about-gallery {
	padding-bottom: 28px;
	font-size: 16px;
	@include xl {
		font-size: 13px; }
	@include md {
		font-size: 10px; }
	@include sm {
		font-size: 9px; }
	@media only screen and (max-width: 480px) {
		font-size: 8px; }
	@media only screen and (max-width: 420px) {
		font-size: 7px; }


	&__wrap {
		position: relative;
		@include min_lg {
			padding-top: 3.625em;
			min-height: 53.1875em; } }

	&__content-wrap {
		background: #fff;
		position: relative;
		z-index: 1;
		background: #fff;
		@include min_lg {
			max-width: 36.9375em;
			padding: 4.1875em 3.6875em 9.8125em 0.125em; }
		@include lg {
			margin-bottom: 30px; } }


	&__title {
		margin-bottom: 1.375em;
		@include min_lg {
			font-size: 1.75em;
			line-height: 1.875em; }
		@include lg {
			font-size: 28px;
			line-height: 30px; } }

	&__desc {
		@include min_lg {
			font-size: 1.125em;
			line-height: 1.875em; }
		@include lg {
			font-size: 16px;
			line-height: 26px; } }

	&__images {
		@include lg {
			position: relative;
			display: flex;
			flex-direction: column;
			align-items: flex-end;
			margin-bottom: 30px; } }

	&__img {
		&-wrap {
			@include min_lg {
				position: absolute; }
			&:nth-child(1) {
				width: 40.9375em;
				height: 30.25em;
				@include min_lg {
					top: 0;
					right: 0; }
				@include lg {
					margin-bottom: 20px; }
				@include xsm {
					width: 247px;
					height: 192px; } }
			&:nth-child(2) {
				display: flex;
				flex-direction: column;
				align-items: flex-end;
				width: 16.25em;
				height: 22.25em;
				z-index: 2;
				@include min_lg {
					top: 24.8125em;
					right: 31.8125em; }
				@include lg {
					position: absolute;
					bottom: 31px;
					left: 0; }
				@include sm {
					left: auto;
					right: 300px; }
				@media only screen and (max-width: 480px) {
					right: 250px; }
				@media only screen and (max-width: 420px) {
					right: 220px; }
				@include xsm {
					width: 95px;
					height: 133px;
					right: auto;
					left: 0; } }

			&:nth-child(3) {
				z-index: 2;
				width: 28.1875em;
				height: 18.75em;
				@include min_lg {
					right: 0px;
					bottom: 0; }
				@include xsm {
					width: 165px;
					height: 112px; } } } }

	&__small-desc {
		font-family: $roboto;
		font-style: italic;
		font-weight: normal;
		font-size: 14px;
		line-height: 22px;
		color: $c1;
		@include min_lg {
			max-width: 20.9375em;
			margin-top: 2.25em;
			margin-right: -3px; }
		@include lg {
			max-width: 300px; } } }
