.partners {
	padding: 64px 0 210px;
	@include xl {
		padding: 50px 0 150px; }
	@include lg {
		padding: 50px 0 110px; }

	&__lg-desc {
		max-width: 1034px;
		margin-bottom: 80px;
		@include lg {
			margin-bottom: 60px; }
		@include sm {
			line-height: 26px;
			margin-bottom: 40px; }
		p {
			margin-bottom: 0;
			&:not(:last-child) {
				margin-bottom: 30px; } } }

	&__info-item {
		&:not(:last-child) {
			margin-bottom: 80px;
			@include lg {
				margin-bottom: 50px; } } } }
