.main {
	&__bg-wrap {
		@include lg {
			background: url('../img/index/responsive/bg.jpg') no-repeat center center;
			background-size: cover; }
		@include sm {
			background: url('../img/index/responsive/bg-576.jpg') no-repeat center center; } }


	&__wrap {
		position: relative;
		z-index: 1;
		display: flex;
		flex-direction: column;
		align-items: center;
		// padding: 16.5vh 0 7vh
		padding: 14vh 0 6vh;
		@include h840 {
			padding: 10vh 0 4vh; }
		@include xxl {
			padding: 11.994vh 0 4vh; }
		@include lg {
			padding: 160px 0 164px; }
		@include sm {
			padding: 150px 0 130px; }
		@include xsm {
			padding: 140px 0 100px; } }


	&__mobile-container {
		@include lg {
			padding-top: 16px;
			padding-bottom: 16px; }
		@include sm {
			padding-top: 16px;
			padding-bottom: 36px; } }

	&__logo {
		@include min_lg {
			opacity: 0;
			margin-bottom: 5.5vh; }
		@include lg {
			display: none; } }

	&__title {
		text-align: center;
		@include min_lg {
			opacity: 0;
			margin-bottom: 1.5vh; }
		@include lg {
			margin-bottom: 10px; } }

	&__sub-title {
		font-size: 26px;
		line-height: 40px;
		text-align: center;
		color: $c0;
		@include xxl {
			font-size: 24px;
			line-height: 36px; }
		@include min_lg {
			margin-bottom: 4.1vh;
			opacity: 0; }
		@include lg {
			margin-bottom: 40px;
			font-size: 22px;
			line-height: 28px; }
		@include md {
			font-size: 20px;
			line-height: 24px; }
		@include sm {
			font-size: 16px;
			line-height: 20px; } }


	&__btn-wrap {
		display: flex;
		align-items: center;
		margin-bottom: 24.5vh;
		@include min_lg {
			opacity: 0; }
		@include xxl {
			margin-bottom: 27.5vh; }
		@include h800 {
			margin-bottom: 20vh; }
		@include h730 {
			margin-bottom: 23vh; }
		@include xl {
			margin-bottom: 23vh; }
		@include lg {
			margin: 0 auto;
			justify-content: center; }
		@include md {
			flex-direction: column; } }


	&__btn {
		&:first-child {
			@include min_md {
				margin-right: 20px; }
			@include md {
				order: 1; } }
		&:last-child {
			@include md {
				margin-bottom: 10px;
				order: 0; } }
		@include md {
			width: 100%; } } }
