.big-nav {
	&__list {}
	li {
		&:not(:last-child) {
			margin-bottom: 2.860vh;
			@include xxl {
				margin-bottom: 2vh; }
			@include h840 {
				margin-bottom: 2vh; }
			@include lg {
				margin-bottom: 15px; } } }
	a {
		font-weight: bold;
		font-size: 60px;
		line-height: 60px;
		color: $c1;
		@include xxl {
			font-size: 42px;
			line-height: 42px; }
		@include h730 {
			font-size: 42px;
			line-height: 42px; }
		@include lg {
			font-size: 36px;
			line-height: 36px; }
		@include md {
			font-size: 28px;
			line-height: 28px; }
		@include sm {
			font-size: 22px;
			line-height: 22px; }
		&:hover {
			color: rgba($c1, 0.9); } } }
