.hamburger {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 72px;
	height: 72px;
	padding: 0;
	border-radius: 50%;
	background: #fff;
	&:focus {
		outline: none; }
	&--green {
		background: $c2;
		box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.15);
		.hamburger {
			&-box {}
			&-inner {
				background: #fff;
				&:after,
				&:before {
					background: #fff; } } } }
	&--small {
		display: none;
		@include lg {
			width: 50px;
			height: 50px;
			display: flex; }
		.hamburger {
			&-box {
				@include lg {
					width: 16px;
					height: 16px; } }
			&-inner {
				@include lg {
					width: 16px;
					height: 2px; }
				&:after,
				&:before {
					@include lg {
						width: 16px;
						height: 2px; } }
				&:before {
					@include lg {
						top: 6px; } }
				&:after {
					@include lg {
						top: 12px; } } } }
		&.hamburger--slider {
			&.is-active {
				.hamburger-inner:after {
					@include lg {
						transform: translate3d(0, -12px, 0) rotate(-90deg); } } } } } }
