.menu {
	// height: 100%
	width: 100%;
	@include min_lg {
		display: flex;
		flex-direction: column;
		justify-content: space-between; }
	@include lg {
		overflow: auto; }

	&__row {
		display: flex;
		flex-wrap: wrap;
		@include min_lg {
			padding-left: 7.813vw;
			padding-right: 5.781vw; }
		&--top {
			align-items: center;
			justify-content: space-between;
			padding-left: 0;
			@include lg {
				margin-bottom: 30px; } }
		&--center {
			// margin-top: -44px
			.menu__col {
				&--left {
					@include lg {
						margin-bottom: 20px; } }
				&--right {
					@include lg {
						margin-bottom: 50px; } } } }
		&--bottom {
			@include lg {
				display: none; }
			.menu__col--right {
				@include lg {
					display: none; } } } }

	&__mobile-title {
		font-weight: bold;
		font-size: 18px;
		line-height: 19px;
		letter-spacing: 0.02em;
		color: $c1;
		@include min_lg {
			display: none; } }


	&__top-col {
		&--left {}
		&--right {} }
	&__col {
		&--left {
			flex-basis: 39.9%;
			max-width: 39.9%;
			@include lg {
				flex-basis: 100%;
				max-width: 100%; } }
		&--right {
			padding-top: 2px;
			flex-basis: 60.1%;
			max-width: 60.1%;
			@include lg {
				flex-basis: 100%;
				max-width: 100%;
				padding-top: 0; } }
		&--mobile {
			@include min_lg {
				display: none; } } }

	&__logo {
		@include lg {
			display: none; } }


	&__info-wrap {
		display: flex;
		align-items: flex-start; }

	&__btn {
		padding: 15.5px 45.05px;
		&-wrap {
			@include lg {
				margin-bottom: 40px; } } }

	&__feedback-wrap {
		@include min_lg {
			margin-right: 2.604vw; }
		@include lg {
			margin-bottom: 30px; } }

	&__address {
		@include min_lg {
			display: none; }
		@include lg {
 }			// margin-bottom: 50px
		@include sm {
			display: none !important; } }
	&__phone {
		margin-bottom: 4px; }

	&__email {
		padding-left: 35px;
		@include sm {
			display: none !important; } } }
