.services {
	position: relative;
	padding: 64px 0 210px;
	@include xl {
		padding: 50px 0 160px; }
	@include lg {
		padding: 50px 0 110px; }


	&__lg-desc {
		max-width: 1034px;
		margin-bottom: 52px;
		@include xl {
			margin-bottom: 40px; }
		@include lg {
			margin-bottom: 30px; }
		p {
			margin-bottom: 0;
			&:not(:last-child) {
				margin-bottom: 30px; } } }

	&__small-title {
		font-weight: bold;
		font-size: 18px;
		line-height: 32px;
		color: $c1;
		margin-bottom: 100px;
		@include xl {
			margin-bottom: 60px; }
		@include lg {
			margin-bottom: 50px; } } }
