.header {
	padding: 20px 0;
	width: 100%;
	@include min_lg {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		z-index: 2; }
	@include lg {
		padding: 15px 0;
		background: #fff;
		border-bottom: 1px solid #E9E9E9; }
	&__row {
		align-items: center;
		justify-content: space-between; }

	&__col {
		padding: 0 15px;
		@include sm {
			padding: 0 20px; }

		&--left {
			flex-basis: 28.3%;
			max-width: 28.3%;
			@include xl {
				flex-basis: 35.5%;
				max-width: 35.5%; } }
		&--center {
			@include lg {
				display: none; } } }

	&__logo {
		margin-right: 60px;
		@include lg {
			display: none; }
		&-wrap {
			display: flex;
			align-items: center; } }

	&__phone {
		@include lg {
			display: none; }
		.phone__icon-wrap {
			margin-right: 12px; } }
	&__btn {
		@include lg {
			display: none; } } }
