.info-item {

	&__row {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin: 0 -35px;
		@include xl {
			margin: 0 -25px; }
		@include lg {
			margin: 0 -20px; }

		&--normal {
			@include min_lg {
				flex-direction: row; } }
		&--reverse {
			@include min_lg {
				flex-direction: row-reverse; }
			.info-item {
				&__col--content {
					@include min_lg {
						display: flex;
						flex-direction: column;
						align-items: flex-end; } } } } }

	&__col {
		padding: 0 35px;
		flex-basis: 50%;
		max-width: 50%;
		@include xl {
			padding: 0 25px; }
		@include lg {
			flex-basis: 100%;
			max-width: 100%; }
		@include lg {
			padding: 0 20px; } }


	&__img {
		&-wrap {
			@include xl {
				display: flex;
				align-items: center; }
			@include lg {
				margin-bottom: 30px; }
			&--long {
				@include min_lg {
					height: 600px; } }
			&--short {
				@include min_lg {
					height: 356px; } } } }

	&__title {
		font-weight: bold;
		font-size: 26px;
		line-height: 38px;
		color: $accent;
		margin-bottom: 19px;
		max-width: 425px;
		display: block;
		text-align: left;
		@include lg {
			margin-bottom: 15px; } }


	&__desc {
		font-size: 16px;
		line-height: 26px;
		color: #726F69;
		max-width: 425px;
		@include sm {
			br {
				display: none; } }
		p {
			margin-bottom: 0; } } }
