.custom-col {
	padding: 0 15px;
	margin-bottom: 100px;
	display: flex;
	justify-content: center;
	align-items: center;
	@include lg {
		margin-bottom: 80px; }
	@include md {
		margin-bottom: 50px; }
	@include sm {
		padding: 0 20px; }
	&--big {
		flex-basis: 33.333%;
		max-width: 33.333%;
		@include md {
			flex-basis: 50%;
			max-width: 50%; }
		@include sm {
			flex-basis: 100%;
			max-width: 100%; } }

	&--small {
		flex-basis: 25%;
		max-width: 25%;
		@include lg {
			flex-basis: 33.333%;
			max-width: 33.333%; }
		@include md {
			flex-basis: 50%;
			max-width: 50%; }
		@include sm {
			flex-basis: 100%;
			max-width: 100%; } } }
