.about {
	padding: 78px 0 194px;
	@include xl {
		padding: 60px 0 160px; }
	@include sm {
		padding: 50px 0 70px; }

	&__container {
		position: relative;
		z-index: 2; }

	&__title {
		font-size: 32px;
		line-height: 38px;
		color: $accent;
		margin-bottom: 31px;
		@include xl {
			margin-bottom: 20px; }
		@include lg {
			font-size: 26px;
			line-height: 36px; } }

	&__desc {
		margin-bottom: 80px;
		@include xl {
			margin-bottom: 60px; }
		@include sm {
			line-height: 26px;
			margin-bottom: 50px; } }

	&__row {
		position: relative;
		justify-content: space-between;
		@include min_lg {
			padding-bottom: 49px;
			margin-bottom: 80px; }
		@include lg {
			margin-bottom: 70px; }
		&:after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			width: calc(100% - 30px);
			height: 1px;
			background: #EEE8D9;
			@include lg {
				display: none; } } }

	&__col {
		padding: 0 15px;
		@include sm {
			padding: 0 20px; }

		&:not(:last-child) {
			@include lg {
				margin-bottom: 30px; } }
		@include lg {
			flex-basis: 100% !important;
			max-width: 100% !important; }
		&--left {
			flex-basis: 31%;
			max-width: 31%;
			@include xl {
				flex-basis: 33.33%;
				max-width: 33.33%; } }

		&--center {
			flex-basis: 27.1%;
			max-width: 27.1%;
			@include xl {
				flex-basis: 33.33%;
				max-width: 33.33%; } }

		&--right {
			flex-basis: 31.3%;
			max-width: 31.3%;
			@include xl {
				flex-basis: 33.33%;
				max-width: 33.33%; } } }

	&__lg-title {
		color: $accent;
		line-height: 38px;
		margin-bottom: 51px;
		@include xl {
			margin-bottom: 30px; }
		@include sm {
			line-height: 30px; } }

	&__md-title {
		margin-bottom: 52px;
 }		// +sm

	&__banner-wrap {
		position: relative;
		display: flex;
		align-items: flex-end;
		height: 530px;
		background: radial-gradient(50% 50% at 50% 50%, rgba(126, 98, 20, 0.34) 0%, rgba(255, 255, 255, 0.04) 83.33%), #7E6214;
		padding: 0 0 50px 60px;
		margin-bottom: 79px;
		@include xl {
			height: 480px; }
		@include lg {
			padding: 0 0 40px 40px;
			height: 500px; }
		@include sm {
			margin-bottom: 75px;
			margin-left: -20px;
			margin-right: -20px;
			height: 510px;
			padding: 0 0 35px 40px; } }


	&__banner-img {
		&-wrap {
			position: absolute;
			left: 50%;
			bottom: 0;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 799px;
			height: 451px;
			transform: translateX(-50%);
			margin-left: -22px;
			@include xl {
				width: 650px;
				height: 367px;
				margin-left: 0;
				bottom: auto;
				@include min_lg {
					top: 50%;
					transform: translate(-50%, -50%); } }
			@include lg {
				top: 50px;
				width: 601px;
				height: 340px; }
			@include sm {
				width: 500px;
				height: 283px;
				top: 110px; }
			@include xsm {
				width: 438px;
				height: 248px; } } }


	&__play-btn {
		display: flex;
		align-items: center;
		&-text {
			font-weight: bold;
			font-size: 18px;
			line-height: 23px;
			color: #FFFFFF;
			max-width: 115px; } }

	&__icon-wrap {
		width: 100px;
		height: 100px;
		min-width: 100px;
		min-height: 100px;
		margin-right: 30px;
		display: flex;
		justify-content: center;
		align-items: center;
		.icon {
			width: 100%;
			height: 100%;
			fill: #fff; } }

	&__md-desc {
		max-width: 1125px;
		margin-bottom: 78px;
		@include sm {
			margin-bottom: 90px; }
		p {
			margin-bottom: 0;
			&:not(:last-child) {
				margin-bottom: 31px;
				@include sm {
					margin-bottom: 26px; } } } } }
