.small-nav {
	&__list {}
	li {
		&:not(:last-child) {
			margin-bottom: 2.860vh;
			@include xxl {
				margin-bottom: 2vh; }
			@include h840 {
				margin-bottom: 2vh; }
			@include lg {
				margin-bottom: 15px; }
			@include md {
				margin-bottom: 13px; }
			@include sm {
				margin-bottom: 10px; } } }

	a {
		font-weight: bold;
		font-size: 36px;
		line-height: 36px;
		color: $accent;
		@include xxl {
			font-size: 24px;
			line-height: 24px; }
		@include h730 {
			font-size: 24px;
			line-height: 24px; }
		@include lg {
			font-size: 22px;
			line-height: 22px; }
		@include md {
			font-size: 20px;
			line-height: 20px; }
		@include sm {
			font-size: 16px;
			line-height: 16px; }
		&:hover {
			color: lighten($accent, 5%); } } }
