.services-info {
	font-size: 14px;
	@include lg {
		font-size: 11px; }
	@include md {
		font-size: 10px; }
	// +sm
	// 	font-size: 9px
	// +xsm
	// 	font-size: 8px

	&__row {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		flex-direction: row;
		&--reverse {
			@include min_lg {
				flex-direction: row-reverse;
				.services-info {
					&__col--left {
						display: flex;
						justify-content: flex-end;
						align-items: center; } } } }
		&:not(:last-child) {
			margin-bottom: 45px; }
		&:nth-child(1) {
			.services-info {
				&__img-wrap {
					@include min_sm {
						width: 37.1875em;
						height: 29.8125em;
						margin-left: -7.125em; }
					@include sm {
						margin-left: -20px; } } } }
		&:nth-child(2) {
			&:not(:last-child) {
				margin-bottom: 60px; }
			.services-info {
				&__img-wrap {
					@include min_sm {
						width: 33.9375em;
						height: 37.875em;
						margin-right: -3.75em; }
					@include min_lg {
						margin-bottom: -55px; }
					@include sm {
						margin-bottom: 0px;
						margin-left: -20px; } } } }

		&:nth-child(3) {
			.services-info {
				&__img-wrap {
					@include min_sm {
						width: 42.6875em;
						height: 27.9375em;
						margin-left: -11.875em; }
					@include min_lg {
						margin-bottom: -17px; } } } } }

	&__col {
		&--left {
			flex-basis: 49%;
			max-width: 49%;
			@include lg {
				flex-basis: 100%;
				max-width: 100%; } }
		&--right {
			flex-basis: 51%;
			max-width: 51%;
			@include lg {
				flex-basis: 100%;
				max-width: 100%; } } }

	&__img {
		@include sm {
			display: none; }
		&--mobile {
			@include min_sm {
				display: none; }
			@include sm {
				display: block; } }
		&-wrap {
			@include lg {
				margin-bottom: 40px; } } }

	&__title {
		font-weight: bold;
		font-size: 32px;
		line-height: 42px;
		color: $accent;
		margin-bottom: 44px;
		@include xl {
			font-size: 30px;
			line-height: 36px;
			margin-bottom: 30px; }
		@include lg {
			font-size: 28px;
			line-height: 32px; }
		@include sm {
			font-size: 24px;
			line-height: 30px;
			br {
				display: none; } } } }
